import uniIcon from '../uni-icon/uni-icon.vue'
export default {
	name: "uni-steps",
	components: {
		uniIcon
	},
	props: {
		direction: { //排列方向 row column
			type: String,
			default: 'row'
		},
		activeColor: { //激活状态颜色
			type: String,
			default: '#1aad19'
		},
		active: { //当前步骤
			type: [Number, String],
			default: 0
		},
		data: Array //数据
	},
	data() {
		return {}
	},
	computed: {
		steps() {
			let steps = []
			this.data.forEach((item, index) => {
				let step = {}
				step.title = item.title
				step.desc = item.desc
				step.status = this.getStatus(index)
				steps.push(step)
			})
			return steps
		}
	},
	methods: {
		getStatus(index) {
			if (index < Number(this.active)) {
				return 'finish'
			} else if (index === Number(this.active)) {
				return 'process'
			}
			return ''
		}
	}
}
